import { Action } from '@ngrx/store';
import { ObjectOwner, TableDto, SqlField } from 'src/app/shared/models';
import { MetaDataObject } from '../../../shared/models/sql-data';

/**
 * @description Enum of actions related to selected sql object.
 */
export enum ActionTypes {
  LOAD_SQL_OBJECT_DATA = '[DB Manager] load sql object data',
  LOADED_SQL_OBJECT_DATA = '[DB Manager] loaded sql object data',

  LOAD_SQL_OBJECT_STRUCTURE = '[DB Manager] load sql object structure',
  LOADED_SQL_OBJECT_STRUCTURE = '[DB Manager] loaded sql object structure',

  SAVE_SQL_OBJECT_STRUCTURE = '[DB Manager] save sql object structure',
  SAVED_SQL_OBJECT_STRUCTURE = '[DB Manager] saved sql object structure',
  SAVE_SQL_OBJECT_DATA = '[DB Manager] save sql object data',
  UPDATE_SQL_OBJECT_DATA = '[DB Manager] update sql object data',

  DELETE_SQL_OBJECT = '[DB Manager] delete sql object',
  UPDATE_SQL_OBJECT = '[DB Manager] update state of sql object',

  REFRESH_SQL_OBJECT = '[DB Manager] refresh sql object',
  LOAD_STATE_SQL_OBJECT = '[DB Manager] load state',

  SET_SQL_DATA_FILTERS = '[DB Manager] set sql data filters',
  SET_SQL_DATA_ROWS_COUNT = '[DB Manager] set sql data rows count',
  SET_SQL_DATA_FILTER_PROP = '[DB Manager] set sql data filter property'
}

export class LoadSqlObjectDataAction implements Action {
  readonly type = ActionTypes.LOAD_SQL_OBJECT_DATA;

  constructor(public payload?: ObjectOwner) {}
}

export class LoadedSqlObjectDataAction implements Action {
  readonly type = ActionTypes.LOADED_SQL_OBJECT_DATA;

  constructor(public payload: TableDto) {}
}

export class LoadSqlObjectStructureAction implements Action {
  readonly type = ActionTypes.LOAD_SQL_OBJECT_STRUCTURE;

  constructor(public payload: ObjectOwner) {}
}

export class LoadedSqlObjectStructureAction implements Action {
  readonly type = ActionTypes.LOADED_SQL_OBJECT_STRUCTURE;

  constructor(public payload: MetaDataObject) {}
}

export class SaveSqlObjectStructureAction implements Action {
  readonly type = ActionTypes.SAVE_SQL_OBJECT_STRUCTURE;

  constructor(public payload: MetaDataObject) {}
}

export class SaveSqlObjectDataAction implements Action {
  readonly type = ActionTypes.SAVE_SQL_OBJECT_DATA;
}

export class UpdateSqlObjectDataAction implements Action {
  readonly type = ActionTypes.UPDATE_SQL_OBJECT_DATA;

  constructor(public payload: any[]) {}
}

export class DeleteSqlObjectAction implements Action {
  readonly type = ActionTypes.DELETE_SQL_OBJECT;

  constructor(public payload: MetaDataObject) {}
}

export class UpdateSqlObjectAction implements Action {
  readonly type = ActionTypes.UPDATE_SQL_OBJECT;

  constructor(public payload: MetaDataObject) {}
}

export class SavedSqlObjectStructure implements Action {
  readonly type = ActionTypes.SAVED_SQL_OBJECT_STRUCTURE;
}

export class RefreshSqlObject implements Action {
  readonly type = ActionTypes.REFRESH_SQL_OBJECT;
}

export class SetLoadStateForSqlObject implements Action {
  readonly type = ActionTypes.LOAD_STATE_SQL_OBJECT;
}

export class SetSqlDataFiltersAction implements Action {
  readonly type = ActionTypes.SET_SQL_DATA_FILTERS;

  constructor(public payload: any) {}
}

export class SetSqlDataRowsCountAction implements Action {
  readonly type = ActionTypes.SET_SQL_DATA_ROWS_COUNT;

  constructor(public payload: number) {}
}

export class SetSqlDataFilterPropAction implements Action {
  readonly type = ActionTypes.SET_SQL_DATA_FILTER_PROP;

  constructor(public payload: {[key: string]: string|number}) {}
}

export type ActionsUnion = LoadSqlObjectDataAction | LoadedSqlObjectDataAction |
  LoadSqlObjectStructureAction | LoadedSqlObjectStructureAction | SaveSqlObjectDataAction |
  UpdateSqlObjectAction | UpdateSqlObjectDataAction | RefreshSqlObject | SetLoadStateForSqlObject |
  SetSqlDataFiltersAction | SetSqlDataRowsCountAction | SetSqlDataFilterPropAction;
