import { ActionsUnion, ActionTypes } from '../actions/sql-objects';
import { SqlObject, DBConnection } from 'src/app/shared/models';

export interface SqlObjectsState {
  list: SqlObject[];
}

const initState: SqlObjectsState = {
  list: []
};

export function sqlObjectsReducer(state: SqlObjectsState = initState, action: ActionsUnion): SqlObjectsState {
  switch (action.type) {
    case ActionTypes.LOADED_SQL_OBJECTS: {
      const payload = action.payload;
      return {
        ...state,
        list: payload
      };
    }

    default: {
      return state;
    }
  }
}

export interface ConnectionsState {
  list: DBConnection[];
  current: string;
}

const initConnections: ConnectionsState = {
  list: [],
  current: null
};

export function connectonsReducer(state: ConnectionsState = initConnections, action: ActionsUnion) {
  switch (action.type) {
    case ActionTypes.SELECT_CONNECTION: {
      const payload = action.payload;
      return {
        ...state,
        current: payload
      };
    }

    case ActionTypes.LOADED_CONNECTIONS: {
      const payload = action.payload;

      return {
        ...state,
        list: payload
      };
    }

    default: {
      return state;
    }
  }
}
