import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, switchMap, map, withLatestFrom } from 'rxjs/operators';

import { SqlDbService } from 'src/app/shared/services';
import { TableDto, MetaDataObject } from 'src/app/shared/models';
import { environment } from 'src/environments/environment';
import { ActionTypes, LoadedSqlObjectStructureAction,
  LoadedSqlObjectDataAction, SavedSqlObjectStructure, LoadSqlObjectDataAction } from '../actions/sql-object';
import * as rootActions from '../actions/index';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../reducers';
import { compareSqlObjects, getErrorMessage, mapValuesToTable } from 'src/app/shared';
import { LoadSqlObjectsAction } from '../actions/sql-objects';
import { Router } from '@angular/router';
import { ObjectType } from '../../models';

const API_URL = `${environment.apiBaseUrl}/sql`;

@Injectable()
export class SqlObjectEffects {
  constructor(
    private actions$: Actions,
    private sqlDb: SqlDbService,
    private store: Store<DBManagerState>,
    private router: Router) {}

  @Effect()
  loadSqlObjectStructure$ = this.actions$.pipe(
    ofType<any>(ActionTypes.LOAD_SQL_OBJECT_STRUCTURE),
    withLatestFrom(this.store),
    switchMap(([{payload}, {dbConnections: {current}}]) =>
      this.sqlDb.getObject(`${API_URL}/${current}`, payload.objectOwner, payload.objectName, payload.objectType).
      pipe(
        map(metaData => new LoadedSqlObjectStructureAction(metaData as MetaDataObject)),
        catchError(e => of(
          new rootActions.ErrorAction(e.message),
          new LoadedSqlObjectStructureAction(null)
        )))
    ));

  @Effect()
  loadSqlObjectData$ = this.actions$.pipe(
    ofType<any>(ActionTypes.LOAD_SQL_OBJECT_DATA),
    withLatestFrom(this.store),
    switchMap(([{payload}, {dbConnections: {current}, sqlObject: {filters}}]) =>
      this.sqlDb.selectSqlObjectData(current, payload.objectOwner, payload.objectName, filters).
        pipe(
          map(data => new LoadedSqlObjectDataAction(data as TableDto)),
          catchError(e => of(
            new rootActions.ErrorAction(e),
            new LoadedSqlObjectDataAction(null)
          )))
      ));

  @Effect()
  saveSqlObjectStructure$ = this.actions$.pipe(
    ofType(ActionTypes.SAVE_SQL_OBJECT_STRUCTURE),
    withLatestFrom(this.store),
    switchMap(([{payload}, store]) => {
      if (!compareSqlObjects(payload, store.sqlObject.structure)) {
        return this.sqlDb.generateObject(`${API_URL}/${store.dbConnections.current}`, payload).pipe(
          switchMap(dataObject => [
              new LoadSqlObjectsAction(store.dbConnections.current),
              new LoadedSqlObjectStructureAction(dataObject),
              new SavedSqlObjectStructure(),
              new rootActions.SuccessAction('SQL Structure saved'),
            ]),
          catchError(e => {
            return of(new rootActions.ErrorAction(getErrorMessage(e)));
          }));
      }
      return [new SavedSqlObjectStructure(), {type: 'Sql structure not changed'}];
    }));

  @Effect()
  saveSqlObjectData$ = this.actions$.pipe(
    ofType(ActionTypes.SAVE_SQL_OBJECT_DATA),
    withLatestFrom(this.store),
    switchMap(([action, {dbConnections, sqlObject}]) =>
      this.sqlDb.saveSqlObjectData(dbConnections.current, sqlObject.structure,
        {tableData: mapValuesToTable(sqlObject.changedData)}).pipe(
        switchMap(() => [
          new LoadSqlObjectDataAction(sqlObject.structure),
          new rootActions.SuccessAction('SQL Object data saved')]),
        catchError(e => {
          return of(new rootActions.ErrorAction(e.error || 'Server error'));
        }))
    ));

  @Effect()
  deleteSqlObject$ = this.actions$.pipe(
    ofType(ActionTypes.DELETE_SQL_OBJECT),
    withLatestFrom(this.store),
    switchMap(([{payload}, store]) =>
      this.sqlDb.deleteObject(`${API_URL}/${store.dbConnections.current}`, payload).pipe(
        switchMap(() => {
          this.router.navigate(['/database', store.dbConnections.current]);
          return [
            new rootActions.SuccessAction('SQL Object deleted'),
            new LoadSqlObjectsAction(store.dbConnections.current),
            new LoadedSqlObjectDataAction(null),
            new LoadedSqlObjectStructureAction(null)
          ];
        }),
        catchError(e => {
          const errors = [e.error.title, Object.values(e.error.errors)];
          return of(new rootActions.ErrorAction(errors.join('\n')));
        }))
    ));

  @Effect()
  loadedSqlObject$ = this.actions$.pipe(
    ofType(ActionTypes.LOADED_SQL_OBJECT_STRUCTURE),
    withLatestFrom(this.store),
    map(([{payload}, store]) => {
      let urlPath = ['/database', store.dbConnections.current];
      if (payload) {
        const {objectOwner, objectName, objectType} = payload as MetaDataObject;
        if (objectType === ObjectType.View) {
          urlPath.push('view');
        }
        urlPath = [...urlPath, objectOwner, objectName];
        this.router.navigate(urlPath);
      }
      return {type: 'Route checked after table structure update'};
    }));

  @Effect()
  limitRowsCount$ = this.actions$.pipe(
    ofType(ActionTypes.SET_SQL_DATA_FILTER_PROP),
    withLatestFrom(this.store),
    map(([prop, {sqlObject: {structure}}]) => new LoadSqlObjectDataAction(structure)));
  }
