import { ActionsUnion, ActionTypes } from '../actions/sql-object';
import { MetaDataObject, TableDto, SelectBody } from 'src/app/shared/models';
import { LoadingStates } from '../../models';
import { deepClone } from 'src/app/shared';

export interface SqlObjectState {
  structure: MetaDataObject;
  currentState: MetaDataObject;
  data: TableDto;
  changedData: any[];
  filters: SelectBody;
  loading: string[];
  rowsCount: number;
}

const initStructure: MetaDataObject = {
  fields: [],
  details: {},
  objectId: 0,
  objectOwner: null,
  objectName: 'New',
  objectType: 'Table'
};

const initState: SqlObjectState = {
  structure: {...initStructure},
  data: null,
  currentState: {...initStructure},
  changedData: null,
  filters: {
    limitRowsCount: 1000,
    skipRowsCount: 0
  },
  loading: [],
  rowsCount: 0
};

export function sqlObjectsReducer(state: SqlObjectState = initState, action: ActionsUnion): SqlObjectState {
  switch (action.type) {
    case ActionTypes.LOAD_SQL_OBJECT_STRUCTURE: {
      const payload = action.payload;
      return {
        ...state,
        filters: {
          limitRowsCount: state.filters.limitRowsCount,
          skipRowsCount: 0,
        },
        structure: {...initStructure, ...payload},
        currentState: {...initStructure, ...payload},
        loading: Array.from(new Set([...state.loading, LoadingStates.STRUCTURE_LOAD]))
      };
    }

    case ActionTypes.LOAD_SQL_OBJECT_DATA: {
      return {
        ...state,
        data: null,
        loading: Array.from(new Set([
          ...state.loading.filter(l => l !== LoadingStates.DATA_SAVE), LoadingStates.DATA_LOAD
        ]))
      };
    }

    case ActionTypes.SAVE_SQL_OBJECT_DATA: {
      return {
        ...state,
        loading: Array.from(new Set([...state.loading, LoadingStates.DATA_SAVE]))
      };
    }

    case ActionTypes.LOAD_STATE_SQL_OBJECT: {
      return {
        ...state,
        loading: [LoadingStates.STRUCTURE_LOAD, LoadingStates.DATA_LOAD]
      };
    }

    case ActionTypes.LOADED_SQL_OBJECT_STRUCTURE: {
      const structure = action.payload || {...initStructure};
      return {
        ...state,
        structure,
        loading: state.loading.filter(l => l !== LoadingStates.STRUCTURE_LOAD),
        currentState: deepClone(structure)
      };
    }

    case ActionTypes.LOADED_SQL_OBJECT_DATA: {
      const payload = action.payload;
      const data = payload;

      return {
        ...state,
        data,
        loading: state.loading.filter(l => l !== LoadingStates.DATA_LOAD),
        changedData: null
      };
    }

    case ActionTypes.UPDATE_SQL_OBJECT: {
      return {
        ...state,
        currentState: action.payload
      };
    }

    case ActionTypes.UPDATE_SQL_OBJECT_DATA: {
      return {
        ...state,
        changedData: action.payload
      };
    }

    case ActionTypes.REFRESH_SQL_OBJECT: {
      const { structure } = state;
      return {
        ...state,
        changedData: null,
        currentState: deepClone(structure)
      };
    }

    case ActionTypes.SET_SQL_DATA_FILTERS: {
      return {
        ...state,
        filters: {
          limitRowsCount: state.filters.limitRowsCount,
          ...action.payload,
        },
      };
    }

    case ActionTypes.SET_SQL_DATA_ROWS_COUNT: {
      return {
        ...state,
        rowsCount: action.payload
      };
    }

    case ActionTypes.SET_SQL_DATA_FILTER_PROP: {
      return {
        ...state,
        filters: {
          ...state.filters,
          ...action.payload
        }
      };
    }

    default: {
      return state;
    }
  }
}
