import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../../store';
import { selectConnectionsList, selectRouterParam } from '../../store/selectors';
import { Observable } from 'rxjs';
import { DBConnection } from 'src/app/shared/models';

/**
 * @description Compoenents displays list of selectable connection.
 */
@Component({
  selector: 'app-db-connections',
  templateUrl: './db-connections.component.html',
  styleUrls: ['./db-connections.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DbConnectionsComponent implements OnInit {

  connections$: Observable<DBConnection[]>;
  selectedConnection$: Observable<string>;

  constructor(private router: Router, private store: Store<DBManagerState>) { }

  ngOnInit() {
    this.connections$ = this.store.select(selectConnectionsList);
    this.selectedConnection$ = this.store.select(selectRouterParam, 'connectionName');
  }

  onConnectionSelect(connection: string) {
    this.router.navigate(['/database', connection]);
  }

}
