import { createSelector, createFeatureSelector } from '@ngrx/store';

import { DBManagerState } from '../index';
import { SqlObjectsState, ConnectionsState } from '../reducers/sql-objects';
import * as fromRouter from '@ngrx/router-store';

export * from './sql-object';

export const selectSqlObjects = (state: DBManagerState) => {
  return state.sqlObjects;
};

export const selectConnections = (state: DBManagerState) => {
  return state.dbConnections;
};

export const selectSqlObjectsList = createSelector(
  selectSqlObjects,
  (state: SqlObjectsState) => state.list);

export const selectConnectionsList = createSelector(
  selectConnections,
  (state: ConnectionsState) => state.list);

export const selectConnectionName = createSelector(
  selectConnections,
  (state: ConnectionsState) => state.current);

export const selectRouter = createFeatureSelector<DBManagerState, fromRouter.RouterReducerState<any>>('router');

export const {
  selectQueryParams,    // select the current route query params
  selectQueryParam,     // factory function to select a query param
  selectRouteParams,    // select the current route params
  selectRouteParam,     // factory function to select a route param
  selectRouteData,      // select the current route data
  selectUrl,            // select the current url
} = fromRouter.getSelectors(selectRouter);

export const selectRouterParams = createSelector(
  selectRouter,
  (state: fromRouter.RouterReducerState<any>, props: string[] = null) => {
    if (!state.state) {
      return {};
    }
    let route = state.state.root;
    let params = {...route.params};

    while (route.firstChild) {
      route = route.firstChild;
      params = {...params, ...route.params};
    }

    if (!props) {
      return params;
    }

    return Object.entries(params).reduce((prev, [prop, val]) => {
      if (props.includes(prop)) {
        prev[prop] = val;
      }
      return prev;
    }, {});
  });

export const selectRouterParam = createSelector(
  selectRouterParams,
  (params: object, param: string) => params[param]);


