import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { FileManagerService, SqlDbService, NotificationService, DialogService } from 'src/app/shared/services';
import { MetaDataObject, SQLDataTypes, NotificationType, SqlObject, SqlTypesSizes, SqlSaveStatus } from 'src/app/shared/models';
import { AuthService } from 'src/app/security';
import { getFormattedFields, getPrimaryKeys, filterItems, formatToValidSqlName } from '../../utils';
import { DBManagerState } from '../../store';
import { Store } from '@ngrx/store';
import { selectConnectionName, selectSqlObjectsList } from '../../store/selectors';
import { environment } from 'src/environments/environment.extension';
import { switchMap, catchError, map, tap } from 'rxjs/operators';
import { throwError, Observable, of, BehaviorSubject } from 'rxjs';
import { Router } from '@angular/router';
import { FlexGrid, CellRangeEventArgs, CellType } from 'wijmo/wijmo.grid';
import { MapTableColumn, Preprocessors } from '../../models';
import { LoadSqlObjectsAction } from '../../store/actions/sql-objects';
import { getErrorMessage, dateToDtoString, parseDatetimeOrNull, parseNumberOrNull } from 'src/app/shared';
import { PreviewDataComponent } from '../preview-data/preview-data.component';
import Proprocessor, { ProcessFuncToType } from '../../preprocessor';

enum ProgressMessage {
  Create = 'Creating new table',
  Importing = 'Importing data',
  Imported = 'Data successfully imported'
}

@Component({
  selector: 'app-import-csv',
  templateUrl: './import-csv.component.html',
  styleUrls: ['./import-csv.component.scss']
})
export class ImportCsvComponent implements OnInit {

  @Input() modalRef: ModalDirective;
  @ViewChild('grid', { static: true }) grid: FlexGrid;
  readonly dataTypes = Object.values(SQLDataTypes);
  tableColumns: MapTableColumn[];
  metaData: MetaDataObject;
  data: any;
  rowsLoaded: number;
  sourceFields: any[];
  tables$: Observable<SqlObject[]>;
  createTable$ = new BehaviorSubject(true);
  selectedTable: MetaDataObject;
  loading = false;
  progressMessage: string;
  error: string;
  preprocessors = Preprocessors;
  private connection: string;
  private typesDescriptor: {};
  private rowIndex: number;

  constructor(
    private fileService: FileManagerService,
    private authService: AuthService,
    private sqlService: SqlDbService,
    private store: Store<DBManagerState>,
    private notificationService: NotificationService,
    private router: Router,
    private dialogService: DialogService) { }

  ngOnInit() {
    this.metaData = {
      objectId: 0,
      objectName: null,
      objectOwner: this.authService.user.userName,
      objectType: 'Table',
      fields: []
    };

    this.store.select(selectConnectionName).subscribe(connection => {
      this.connection = connection;
    });

    this.tables$ = this.store.select(selectSqlObjectsList);

    this.createTable$.subscribe((val) => {
      if (val) {
        this.createNewTableColumns();
      } else {
        this.createSelectedTableColumns();
      }
    });

    this.grid.addEventListener(this.grid.hostElement, 'contextmenu', (e: CellRangeEventArgs) => {
      const ht = this.grid.hitTest(e);
      this.rowIndex = ht.row;
    });

    this.grid.cellEditEnded.addHandler((g, e: CellRangeEventArgs) => {
      const dataItem = this.grid.rows[e.row].dataItem;
      const binding = this.grid.columns[e.col].binding;
      const newVal = dataItem[binding];

      if (e.panel.cellType === CellType.Cell) {

        switch (binding) {
          case 'dataType': {
            dataItem.size = SqlTypesSizes[newVal] || null; break;
          }
          case 'preprocessFunc': {
            dataItem.dataType = ProcessFuncToType[newVal] || null;
            if (dataItem.dataType) {
              dataItem.size = SqlTypesSizes[dataItem.dataType] || null;
            }
            break;
          }
        }
        this.grid.refreshCells(false);
      }
    });
  }

  get isValid() {
    return this.metaData.objectOwner && this.metaData.objectName && this.metaData.fields.length;
  }

  onCtxMenuClicked() {
    this.grid.collectionView.sourceCollection =
      [...this.grid.collectionView.items.slice(0, this.rowIndex),
      { isKey: false, dataType: SQLDataTypes.NVARCHAR, size: SqlTypesSizes[SQLDataTypes.NVARCHAR] },
      ...this.grid.collectionView.items.slice(this.rowIndex)];
  }

  onTableSelect(table: SqlObject) {
    this.loading = true;
    this.sqlService.getObject(this.sqlConnectionUrl, table.objectOwner, table.objectName).subscribe((metaData) => {
      this.selectedTable = metaData;
      this.createSelectedTableColumns();
      this.loading = false;
    });
  }

  import() {
    const createTable = this.createTable$.value;
    this.loading = true;
    this.progressMessage = createTable ? ProgressMessage.Create : ProgressMessage.Importing;
    const tableColumns = this.grid.collectionView.items;
    const metaData = {
      ...(createTable ? this.metaData : this.selectedTable),
      fields: getFormattedFields(tableColumns),
      primaryKeys: getPrimaryKeys(tableColumns)
    };
    (createTable ? this.sqlService.generateObject(this.sqlConnectionUrl, metaData) : of(null)).pipe(
      catchError(e => {
        const message = getErrorMessage(e) || 'Failed to create new table';
        this.notificationService.open({
          message,
          type: NotificationType.DANGER
        });
        return throwError(message);
      }),
      switchMap(() => this.saveData())).subscribe(
        (res) => this.importHandler(metaData, res),
        (e) => this.importErrorHandler(e));
  }

  previewData() {
    const previewDialog = this.dialogService.open(PreviewDataComponent, { class: 'modal-w-90 full-height d-flex flex-column' });
    previewDialog.config = {
      data: Proprocessor.run(this.data, this.grid.collectionView.items),
      fields: this.grid.collectionView.items
    };
  }

  get sqlConnectionUrl() {
    return `${environment.apiBaseUrl}/sql/${this.connection}`;
  }

  loadCSV() {
    this.fileService.importCSVFile().subscribe((response) => {
      this.error = null;
      if (!response.result) {
        this.loading = true;
        return;
      }
      const { result: { data }, file } = response;
      if (!this.metaData.objectName) {
        this.metaData.objectName = formatToValidSqlName(file.name.replace(/.txt|-|.csv$/g, ''));
      }
      this.data = data.filter(r => r.join('') !== '');
      this.rowsLoaded = this.data.length;
      try {
        this.typesDescriptor = this.getTypes(this.data);
        this.sourceFields = this.data[0].map(sf => formatToValidSqlName(sf));
        this.metaData.fields = getFormattedFields(this.sourceFields);
        if (this.createTable$.value) {
          this.createNewTableColumns();
        } else {
          this.createSelectedTableColumns();
        }
      } catch (e) {
        this.data = null;
        this.sourceFields = null;
        this.metaData.fields = null;
        this.error = e.message;
      } finally {
        this.loading = false;
      }
    });
  }

  getSelectedTableTitle() {
    return this.selectedTable ? `${this.selectedTable.objectOwner}.${this.selectedTable.objectName}` : 'Select table';
  }

  private importHandler(metaData, res: SqlSaveStatus) {
    if (res.savedItemsCount === res.itemsToSaveCount) {
      this.notificationService.open({
        type: NotificationType.SUCCESS,
        message: `Successfully inserted: ${res.inserted.toLocaleString()}
        , updated: ${res.updated.toLocaleString()}
        , deleted: ${res.deleted.toLocaleString()}`
      });
      this.progressMessage = ProgressMessage.Imported;

      if (this.createTable$.value) {
        this.store.dispatch(new LoadSqlObjectsAction(this.connection));
      }

      setTimeout(() => {
        this.router.navigate(['/database', this.connection, metaData.objectOwner, metaData.objectName]);
        this.modalRef.hide();
        this.loading = false;
        this.progressMessage = null;
      }, 1e3);
    } else {
      let mess = res.savedItemsCount ? 'So far ' : '';
      if (res.inserted) {
        mess += `Inserted: ${res.inserted.toLocaleString()} `;
      }
      if (res.updated) {
        mess += `Updated: ${res.updated.toLocaleString()} `;
      }
      if (res.deleted) {
        mess += `Deleted: ${res.deleted.toLocaleString()}`;
      }
      this.progressMessage = `${ProgressMessage.Importing} ${res.itemsToSaveCount.toLocaleString()} rows. ${mess}`;
    }
  }

  private importErrorHandler(e) {
    this.loading = false;
    this.progressMessage = null;
    console.error(e);
  }

  private createSelectedTableColumns() {
    if (this.selectedTable) {
      const primaryKeys = new Set(this.selectedTable.primaryKeys.map(p => p.fieldName));
      this.tableColumns = filterItems(this.selectedTable.fields).map((f, i) => {
        const sourceField = this.sourceFields ? this.sourceFields.find(sf => sf === f.fieldName) : null;
        return {
          fieldName: f.fieldName,
          dataType: f.dataType || SQLDataTypes.NVARCHAR,
          size: f.size,
          sourceField,
          isKey: primaryKeys.has(f.fieldName),
          preprocessFunc: null,
          preprocessParams: null
        };
      });
    } else {
      this.tableColumns = [];
    }
  }

  private createNewTableColumns() {
    if (this.sourceFields) {
      this.tableColumns = filterItems(this.sourceFields).map((f, i) => {
        const typeDesc = this.typesDescriptor[`col${i}`];
        return {
          fieldName: f.fieldName,
          dataType: f.dataType || typeDesc.type || SQLDataTypes.NVARCHAR,
          size: f.size || typeDesc.size,
          sourceField: f.fieldName,
          isKey: f.isKey || Boolean(typeDesc.uniqueValues),
          preprocessFunc: null,
          preprocessParams: null
        };
      });
    }
  }

  // what type does it return?
  private saveData() {
    this.progressMessage = ProgressMessage.Importing;
    const dataObject = this.createTable$.value ? this.metaData : this.selectedTable;
    return this.sqlService.saveSqlObjectDataWithStatus(this.sqlConnectionUrl, dataObject, this.sendData).pipe(
      catchError(e => {
        const message = typeof e.error === 'string' ? e.error : 'Failed to Import';
        this.notificationService.open({
          message,
          type: NotificationType.DANGER
        });
        return throwError(message);
      }));
  }

  // what type does it return?
  private get sendData() {
    const items = this.grid.collectionView.items;
    const fields = items.map(({ fieldName }) => fieldName);
    const indexs = fields.map((f, i) => f ? i : -1).filter(i => i !== -1);

    return {
      tableData: {
        fieldNames: fields.filter(f => f),
        rows: Proprocessor.run(this.data, items)
          .map(arr => arr.filter((v, i) => indexs.includes(i)))
          .map(rr => rr.map(v => v instanceof Date ? dateToDtoString(v) : v))
      }
    };
  }

  // what type does it return?
  private getTypes(data) {
    const typesDescriptor = data[0].reduce((prev: object, fieldName: string, index: number) => {
      prev[`col${index}`] = {
        fieldName,
        index,
        type: null,
        size: null,
        uniqueValues: new Set()
      };
      return prev;
    }, {});
    for (let index = 1; index < data.length; index++) {
      const item = data[index];
      item.forEach((val, i) => {
        typesDescriptor[`col${i}`] = this.getType(typesDescriptor[`col${i}`], val);
      });
    }
    let isUniqueSet = false;
    Object.values(typesDescriptor).forEach((v: any) => {
      v.uniqueValues = v.uniqueValues && !isUniqueSet;
      if (v.uniqueValues) {
        isUniqueSet = true;
      }
    });
    return typesDescriptor;
  }

  private getType(typeInfo, val) {
    if (typeInfo.uniqueValues && typeInfo.uniqueValues.has(val)) {
      typeInfo.uniqueValues = false;
    } else if (typeInfo.uniqueValues) {
      typeInfo.uniqueValues.add(val);
    }
    // ignore empty values
    if (!val || !String(val).length) { return typeInfo; }

    const intOrBigIntFn = (intNum: number): SQLDataTypes.INT | SQLDataTypes.BIGINT => {
      return (intNum > 2147483647) ? SQLDataTypes.BIGINT : SQLDataTypes.INT;
    };

    const numFn = (nn: number) => {
      if (typeInfo.type === null || typeInfo.type === SQLDataTypes.INT) {
        typeInfo.type = Number.isInteger(nn) ? intOrBigIntFn(nn) : SQLDataTypes.FLOAT;
      } else if ((typeInfo.type === null || typeInfo.type === SQLDataTypes.BIGINT)
        && Number.isInteger(nn)) {
        typeInfo.type = SQLDataTypes.BIGINT;
      } else if (typeInfo.type === null || typeInfo.type === SQLDataTypes.FLOAT) {
        typeInfo.type = SQLDataTypes.FLOAT;
      }
      return typeInfo;
    };

    switch (typeof val) {
      case 'number': {
        return numFn(val);
      }
      case 'string': {
        if ((typeInfo.type === null || typeInfo.type === SQLDataTypes.DATE) && parseDatetimeOrNull(val)) {
          typeInfo.type = SQLDataTypes.DATE;
          return typeInfo;
        }

        // try to parse more advanced numbers
        if (typeInfo.type === null || typeInfo.type === SQLDataTypes.FLOAT
          || typeInfo.type === SQLDataTypes.INT
          || typeInfo.type === SQLDataTypes.BIGINT) {
          const num = parseNumberOrNull(val);
          if (num !== null) {
            return numFn(num);
          }
        }

        typeInfo.type = SQLDataTypes.NVARCHAR;
        const size = Math.ceil(val.length / 100) * 100 || 100;
        if (typeInfo.size === null || typeInfo.size < size) {
          typeInfo.size = size;
        }
        return typeInfo;
      }
    }
    return typeInfo;
  }
}
