import { Component, OnInit } from '@angular/core';
import { selectSqlObjectStructure, selectSqlObjectLoadingMessages } from '../../store/selectors';
import { DBManagerState } from '../../store';
import { Observable } from 'rxjs';
import { MetaDataObject } from 'src/app/shared/models';
import { Store } from '@ngrx/store';

@Component({
  selector: 'app-sql-view-object',
  templateUrl: './sql-view-object.component.html',
  styleUrls: ['./sql-view-object.component.scss']
})
export class SqlViewObjectComponent implements OnInit {

  dataObject$: Observable<MetaDataObject>;
  isSQLChanged$: Observable<boolean>;
  loading$: Observable<any>;
  constructor(private store: Store<DBManagerState>) {}

  ngOnInit() {
    this.dataObject$ = this.store.select(selectSqlObjectStructure);
    this.loading$ = this.store.select(selectSqlObjectLoadingMessages);
  }

}
