import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Store } from '@ngrx/store';
import { Subject, Observable } from 'rxjs';
import { takeUntil, filter } from 'rxjs/operators';
import { AuthService } from 'src/app/security';
import { CellType, FlexGrid, CellRangeEventArgs } from 'wijmo/wijmo.grid';
import { CollectionView } from 'wijmo/wijmo';
import { DBManagerState } from '../../store';
import { selectSqlObjectStructure, selectSqlObjectState, selectIsChangedSqlObjectStructure } from '../../store/selectors';
import { MetaDataObject, TableColumn, SQLDataTypes, SqlTypesSizes } from '../../../shared/models';
import { SaveSqlObjectStructureAction, UpdateSqlObjectAction, DeleteSqlObjectAction } from '../../store/actions/sql-object';
import { compareSqlObjects, getTableColumns, verifyFields } from 'src/app/shared';
import { getFormattedFields, getPrimaryKeys, getFormattedColumns } from '../../utils';
import { DialogService } from 'src/app/shared/services';

/**
 * @description Component represents editable table structure.
 */
@Component({
  selector: 'app-table-editor',
  templateUrl: './table-editor.component.html',
  styles: [`
    .table-title {
      flex: 0;
    }
  `]
})
export class TableEditorComponent implements OnInit, OnDestroy {
  readonly dataTypes = Object.values(SQLDataTypes);
  @ViewChild('form', {static: false}) form: NgForm;
  @ViewChild('grid', {static: false}) grid: FlexGrid;
  dataObject: MetaDataObject;
  tableColumns: TableColumn[];
  collection: CollectionView = new CollectionView();
  isChanged$: Observable<boolean>;
  private editCollectionMap = new Map<object, object>();
  private addCollectionSet = new Set();
  private destroy$ = new Subject();

  constructor(
    private store: Store<DBManagerState>,
    private authService: AuthService,
    private dialogService: DialogService) { }

  ngOnInit() {
    this.store.select(selectSqlObjectStructure).pipe(
      takeUntil(this.destroy$),
      filter(o => Boolean(o))).subscribe(obj => {
        this.editCollectionMap.clear();
        this.addCollectionSet.clear();
        if (this.grid) {
          this.grid.refreshCells(false);
        }
      });

    this.store.select(selectSqlObjectState).pipe(
      takeUntil(this.destroy$)).subscribe(obj => {
        if (!compareSqlObjects(this.dataObject, obj)) {
          this.dataObject = obj;
          const items = getTableColumns(obj);

          if (!verifyFields(getFormattedColumns(this.collection.items), items)) {
            this.collection.sourceCollection = items;
          }
        }

      if (!this.dataObject.objectOwner && this.authService.user) {
        this.dataObject.objectOwner = this.authService.user.userName;
        this.store.dispatch(new UpdateSqlObjectAction(this.dataObject));
      }
    });

    this.isChanged$ = this.store.select(selectIsChangedSqlObjectStructure);
  }

  formatItem = (panel, r, c, cell) => {
    const dataItem = panel.rows[r].dataItem;
    const binding = panel.columns[c].binding;
    const initData = this.editCollectionMap.get(dataItem);
    if (panel.cellType === CellType.Cell && dataItem) {
      if (this.addCollectionSet.has(dataItem)) {
        cell.classList.add('added-item');
      } else if (initData && initData.hasOwnProperty(binding)) {
        cell.classList.add('edited-item');
      } else {
        cell.classList.remove('added-item');
        cell.classList.remove('edited-item');
      }
    }
  }

  /**
   * TODO: use `initChangesHighlight` from 'src/app/wijmo/utils'.
   */
  initializedGrid(grid: FlexGrid) {
    this.collection.trackChanges = true;
    grid.rowAdded.addHandler((g, e) => {
      this.addCollectionSet.add(this.collection.currentAddItem);
      grid.refreshCells(false);
    });
    grid.cellEditEnded.addHandler((g, e: CellRangeEventArgs) => {
      const item = this.collection.currentEditItem;
      const dataType = item.dataType;
      const size = SqlTypesSizes[dataType] || null;
      const sizeBinding = grid.columns[3].binding;
      const sizePrev = this.collection.currentEditItem[sizeBinding];

      if (!this.addCollectionSet.has(item)) {
        const binding = grid.columns[e.col].binding;
        const newVal = grid.rows[e.row].dataItem[binding];
        const initDataItem = this.editCollectionMap.get(item) || {};
        if (!initDataItem.hasOwnProperty(binding)) {
          initDataItem[binding] = e.data;
        }

        if (initDataItem[binding] === newVal) {
          delete initDataItem[binding];
        }

        // Set default size.
        if (e.panel.cellType === CellType.Cell && e.col === 2) {
          if (!initDataItem.hasOwnProperty(sizeBinding)) {
            initDataItem[sizeBinding] = sizePrev;
          }

          if (initDataItem[sizeBinding] === size) {
            delete initDataItem[sizeBinding];
          }
        }
        this.editCollectionMap.set(item, initDataItem);
      }

      if (e.panel.cellType === CellType.Cell && e.col === 2) {
        item[sizeBinding] = size;
      }
      grid.refreshCells(false);
      this.onFieldsUpdate();
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  save() {
    this.form.ngSubmit.emit();
  }

  onTableDelete() {
    if (this.dataObject) {
      const { objectName, objectOwner } = this.dataObject;
      const message = `Delete table ${objectOwner}.${objectName}. Are you sure?`;
      this.dialogService.confirmation(message).subscribe(val => {
        if (val) {
          this.store.dispatch(new DeleteSqlObjectAction(this.dataObject));
        }
      });
    }
  }

  onSubmit() {
    if (this.form.valid) {
      this.store.dispatch(new SaveSqlObjectStructureAction(this.getCurrentState()));
    }
  }

  onUpdate() {
    this.store.dispatch(new UpdateSqlObjectAction(this.getCurrentState()));
  }

  onFieldsUpdate() {
    this.store.dispatch(new UpdateSqlObjectAction(this.getCurrentState()));
  }

  private getCurrentState(): MetaDataObject {
    return {
      ...this.dataObject,
      fields: getFormattedFields(this.collection.items),
      primaryKeys: getPrimaryKeys(this.collection.items)
    };
  }
}
