import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';

import { DBManagerState } from '../../store';
import { LoadSqlObjectsAction } from '../../store/actions/sql-objects';
import { selectSqlObjectsList, selectRouterParam, selectSqlObjectStructure } from '../../store/selectors';
import { Observable, Subject } from 'rxjs';
import { SqlObject } from 'src/app/shared/models';
import { takeUntil, filter } from 'rxjs/operators';
import { ListViewComponent } from 'src/app/shared';
import { RefreshSqlObject, SetLoadStateForSqlObject } from '../../store/actions/sql-object';
import { DialogService } from 'src/app/shared/services';
import { ImportCsvComponent } from '../import-csv/import-csv.component';

/**
 * @description Component represents list of sql objects.
 */
@Component({
  selector: 'app-sql-objects',
  templateUrl: './sql-objects.component.html',
  styleUrls: ['./sql-objects.component.scss']
})
export class SqlObjectsComponent implements OnInit, OnDestroy {

  @ViewChild('listView', {static: true}) listView: ListViewComponent;
  list$: Observable<SqlObject[]>;
  private destroy$ = new Subject();
  connectionName: string;

  constructor(
    private store: Store<DBManagerState>,
    private dialogService: DialogService) { }

  ngOnInit() {
    this.list$ = this.store.select(selectSqlObjectsList).pipe(takeUntil(this.destroy$));
    this.store.select(selectRouterParam, 'connectionName').pipe(
      filter(val => Boolean(val)), takeUntil(this.destroy$)).
      subscribe(val => {
        this.connectionName = val;
        this.store.dispatch(new LoadSqlObjectsAction(this.connectionName));
      });
    this.store.select(selectSqlObjectStructure).pipe(
      takeUntil(this.destroy$)).
      subscribe(sqlObject => {
        this.listView.selectItem(sqlObject);
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onSelect(obj: SqlObject) {
    const selectedItem = this.listView.selectedItem;
    if (selectedItem) {
      const {objectOwner, objectName} = selectedItem;
      if (objectName !== obj.objectName || objectOwner !== objectOwner) {
        this.store.dispatch(new SetLoadStateForSqlObject());
      }
    } else {
      this.store.dispatch(new SetLoadStateForSqlObject());
    }
  }

  areObjectsEqual(x: SqlObject, y: SqlObject) {
    return x && y && x.objectName === y.objectName && x.objectOwner === y.objectOwner;
  }

  refresh() {
    this.store.dispatch(new RefreshSqlObject());
  }

  import() {
    this.dialogService.open(ImportCsvComponent, {class: 'modal-xl full-height', keyboard: false});
  }
}
