import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../store';
import { selectRouterParam } from '../store/selectors';
import { CanActivate, Router } from '@angular/router';
import { switchMap, map, catchError } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { SqlDbService } from 'src/app/shared/services';
import { LoadedConnections } from '../store/actions/sql-objects';
import { DBConnection } from 'src/app/shared/models';
/**
 * @description Guard validates connection name.
 */
@Injectable()
export class ConnectionGuard implements CanActivate {
  constructor(private store: Store<DBManagerState>, private dbService: SqlDbService, private router: Router) {}

  canActivate(): Observable<boolean> {
    return this.store.select(selectRouterParam, 'connectionName').pipe(
      switchMap((connection) => {
        if (connection) {
          return this.dbService.getMyDbConnections().pipe(
            catchError(e => {
              return of([] as DBConnection[]);
            }),
            map((connections) => {
              try {
                this.store.dispatch(new LoadedConnections(connections));
              } catch (e) {
                return false;
              }
              return connections.map(c => c.connectorName.toLowerCase()).includes(connection.toLowerCase());
            }));
        }
        this.router.navigate(['/login']);
        return of(false);
      }));
  }
}
