import { Component, OnInit, ViewChild, OnDestroy, ElementRef, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CollectionView } from 'wijmo/wijmo';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Ace, edit } from 'ace-builds';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../../store';
import { selectSqlObjectState } from '../../store/selectors';
import { DeleteSqlObjectAction, UpdateSqlObjectAction, SaveSqlObjectStructureAction } from '../../store/actions/sql-object';
import { AuthService } from 'src/app/security';
import { DialogService } from 'src/app/shared/services';
import { MetaDataObject, TableColumn } from 'src/app/shared/models/sql-data';
import { getTableColumns } from 'src/app/shared';
import { ObjectType } from '../../models';

@Component({
  selector: 'app-view-editor',
  templateUrl: './view-editor.component.html',
  styleUrls: ['./view-editor.component.scss']
})
export class ViewEditorComponent implements OnInit, OnDestroy, AfterViewInit {

  dataObject: MetaDataObject;
  tableColumns: TableColumn[];
  collection: CollectionView = new CollectionView();
  isChanged = false;
  @Output() changed = new EventEmitter();
  @ViewChild('form', {static: false}) form: NgForm;
  @ViewChild('sqlEditor', {static: false}) sqlElement: ElementRef;
  private destroy$ = new Subject();
  private sqlEditor: Ace.Editor;
  private initialSql: string;

  constructor(
    private store: Store<DBManagerState>,
    private authService: AuthService,
    private dialogService: DialogService) { }

  ngOnInit() {
    this.store.select(selectSqlObjectState).pipe(
      takeUntil(this.destroy$)).subscribe(obj => {
        this.dataObject = obj;
        this.dataObject.objectType = ObjectType.View;
        this.initialSql = obj.sqlText;
        if (obj.objectName === 'New') {
          this.dataObject.sqlText = 'create view test1.testView1';
        }
        if (this.sqlEditor && typeof obj.sqlText === 'string') {
          this.sqlEditor.setValue(obj.sqlText, -1);
        }
        this.collection.sourceCollection = getTableColumns(obj);
        if (!this.dataObject.objectOwner) {
          this.dataObject.objectOwner = this.authService.user.userName;
          this.store.dispatch(new UpdateSqlObjectAction(this.dataObject));
        }
    });
  }

  ngAfterViewInit() {
    this.sqlEditor = edit(this.sqlElement.nativeElement, {
      mode: 'ace/mode/sql',
      autoScrollEditorIntoView: true,
      value: this.dataObject.sqlText || '0'
    });

    this.sqlEditor.on('change', () => {
      const val = this.sqlEditor.getValue();
      this.isChanged = this.initialSql !== val;
      this.dataObject.sqlText = val;
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }


  onViewDelete() {
    if (this.dataObject) {
      const { objectName, objectOwner } = this.dataObject;
      const message = `Delete view ${objectOwner}.${objectName}. Are you sure?`;
      this.dialogService.confirmation(message).subscribe(val => {
        if (val) {
          this.store.dispatch(new DeleteSqlObjectAction(this.dataObject));
        }
      });
    }
  }

  onSubmit() {
    this.store.dispatch(new SaveSqlObjectStructureAction(this.dataObject));
  }

}
