import { MapTableColumn } from './models';
import { formatToValidSqlName } from './utils';
import * as moment from 'moment';
import { SQLDataTypes } from '../shared/models';
import { parseDatetimeOrNull, parseNumberOrNull } from '../shared';

class Proprocessor {
  run(data: any[][], columns: MapTableColumn[]) {
    const columnIndexMap = data[0].reduce((prev, v, i) => {
      prev[formatToValidSqlName(v)] = i;
      return prev;
    }, {});


    const res = data.map((row, i) => {
      if (i === 0) {
        return row;
      } else {
        return columns.map((column, index) => {
          let val = null;
          if (column.sourceField && typeof columnIndexMap[column.sourceField] === 'number') {
            val = row[columnIndexMap[column.sourceField]];
            if (column.dataType === SQLDataTypes.DATE && !(val instanceof Date)) {
              val = parseDatetimeOrNull(val);
            } else if ((column.dataType === SQLDataTypes.INT
              || column.dataType === SQLDataTypes.BIGINT
              || column.dataType === SQLDataTypes.FLOAT) && typeof val !== 'number') {
              val = parseNumberOrNull(val);
            }
          }
          const { preprocessFunc, preprocessParams } = columns[index];
          if (preprocessFunc && this[preprocessFunc]) {
            return this[preprocessFunc](val, preprocessParams, i);
          }
          return val;
        });
      }
    }).slice(1);
    return res;
  }

  private text(val, param) {
    return param;
  }

  private parseNumber(val, param: string) {
    if (typeof val === 'number') {
      return val;
    } else if (typeof val === 'boolean') {
      return +val;
    } else if (typeof val === 'string') {
      val = val.replace(/,/g, '');
      if (param) {
        param.split('').forEach(v => {
          val = val.replace(v, '');
        });
      }
      const koef = this.getKoef(val);
      try {
        val = parseFloat(val);
        return val * koef;
      } catch (e) {
        return null;
      }
    }
    return null;
  }

  private parseBoolean(val: any) {
    const trulyVals = ['1', 'yes', 'true', 'on'];
    const falsyVals = ['0', 'no', 'false', 'off'];
    const checkVal = val.toString().toLowerCase();

    if (trulyVals.includes(checkVal)) {
      return true;
    }

    if (falsyVals.includes(checkVal)) {
      return false;
    }

    return null;
  }

  private parseDate(val: any, params: string): Date {
    if (!val) {
      return null;
    }

    const date = params ? moment(val, params.split(',')) : moment(val);

    return date.isValid() ? date.toDate() : null;
  }

  /**
   * Generates id based on row index and initial value.
   * @param val Cell value.
   * @param param Initial id value.
   * @param index Row index.
   */
  private autoid(val: any, param: string, index: number) {
    let startIndex = 0;
    try {
      startIndex = parseInt(param, 10);
      if (isNaN(startIndex)) {
        startIndex = 0;
      }
    } catch (e) { }

    return startIndex + index;
  }

  private getKoef(str: string) {
    if (str.includes('k')) {
      return 1000;
    }

    if (str.includes('m')) {
      return 1000000;
    }

    if (str.includes('b')) {
      return 1000000000;
    }

    return 1;
  }

}

export enum ProcessFuncToType {
  'autoid' = SQLDataTypes.INT,
  'text' = SQLDataTypes.NVARCHAR,
  'parseDate' = SQLDataTypes.DATE,
  'parseBoolean' = SQLDataTypes.BIT,
  'parseNumber' = SQLDataTypes.INT
}

export default new Proprocessor();
