import { Component, Input, OnInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { SQLDataTypes, SqlTypesToWijmoMap } from 'src/app/shared/models';
import { mapTableToValues } from 'src/app/shared';

@Component({
  selector: 'app-preview-data',
  templateUrl: './preview-data.component.html',
  styleUrls: ['./preview-data.component.scss']
})
export class PreviewDataComponent implements OnInit {

  @Input() modalRef: ModalDirective;
  @Input() config: {fields: any, data: any};
  data: any;

  ngOnInit() {
    this.data = mapTableToValues({
      rows: this.config.data,
      fieldNames: this.config.fields.map(({fieldName}) => fieldName)
    });
  }

  getColumnType(type: SQLDataTypes) {
    return SqlTypesToWijmoMap[type];
  }
}
