import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { switchMap, takeUntil, withLatestFrom, filter, distinctUntilChanged } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../../store';
import { selectSqlObjectStructure, selectSqlObjectRowsCount, selectSqlObjectLimitRows } from '../../store/selectors';
import { SqlDbService } from 'src/app/shared/services';
import { SetSqlDataRowsCountAction, SetSqlDataFilterPropAction } from '../../store/actions/sql-object';
@Component({
  selector: 'app-rows-count',
  templateUrl: './rows-count.component.html',
  styleUrls: ['./rows-count.component.scss']
})
export class RowsCountComponent implements OnInit, OnDestroy {

  rowsCount$: Observable<number>;
  perPage$: Observable<number|string>;
  private destroy$ = new Subject();

  constructor(private store: Store<DBManagerState>, private dbService: SqlDbService) { }

  ngOnInit() {
    this.rowsCount$ = this.store.select(selectSqlObjectRowsCount);
    this.perPage$ = this.store.select(selectSqlObjectLimitRows);
    this.store.select(selectSqlObjectStructure).pipe(
      takeUntil(this.destroy$),
      filter(obj => Boolean(obj.objectOwner)),
      distinctUntilChanged((a, b) => a.objectOwner === b.objectOwner && b.objectName === a.objectName),
      withLatestFrom(this.store),
      switchMap(([obj, {dbConnections: {current}}]) => {
        return this.dbService.selectSqlObjectData(current, obj.objectOwner,  obj.objectName, {fields: ['count|1']});
      })).subscribe(res => {
        this.store.dispatch(new SetSqlDataRowsCountAction(res.rows[0][0] as any));
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onRowsCountSelect(evt: any) {
    this.store.dispatch(new SetSqlDataFilterPropAction({limitRowsCount: evt.target.value}));
  }

}
