import { TableColumn, SQLDataTypes, SqlField, SqlPrimaryKey, SqlTypesSizes } from 'src/app/shared/models';
import { ObjectType } from './models';

export const filterItems = (items: Array<any>): Array<any> =>
  items.map(i => typeof i !== 'object' ? {fieldName: i} : i).filter(f => f.fieldName);

export function getFormattedColumns(items: Array<any>): TableColumn[] {
  return filterItems(items).map(f => ({
    fieldName: f.fieldName,
    dataType: f.dataType || SQLDataTypes.NVARCHAR,
    size: getDefaultSize(f),
    isKey: f.isKey || false,
    isNullable: f.isNullable || true
  }));
}

export function getFormattedFields(items: Array<any>): SqlField[] {
  return filterItems(items).map(f => ({
    fieldName: f.fieldName,
    dataType: f.dataType || SQLDataTypes.NVARCHAR,
    size: getDefaultSize(f),
    isNullable: f.isNullable || true
  }));
}

export function getPrimaryKeys(items: Array<any>): SqlPrimaryKey[] {
  return items.filter(i => i.isKey).map(({fieldName}) => ({fieldName}));
}

function getDefaultSize(f) {
  return (f.dataType === SQLDataTypes.NVARCHAR || !f.dataType) && !f.size ? SqlTypesSizes[SQLDataTypes.NVARCHAR] : f.size;
}

export function formatToValidSqlName(str: string) {
  return str.replace(/&|\+|\)|\(|\/|@|\:|;|\?|\!|\*|\-/g, '').replace(/\s/g, '_');
}

export function getObjectTypeFromUrl(url: string = ''): ObjectType {
  const match = url.match(/\/database\/[\w-]+\/([\w-]+)\/*/);

  if (match && match[1].toLowerCase() === 'view') {
    return ObjectType.View;
  }
  return ObjectType.Table;
}
