import { ActionReducerMap, MetaReducer } from '@ngrx/store';
import { environment } from 'src/environments/environment';
import * as fromRouter from '@ngrx/router-store';
import * as sqlObjectsRoot from './sql-objects';
import * as sqlObjectRoot from './sql-object';

export interface DBManagerState {
  sqlObjects: sqlObjectsRoot.SqlObjectsState;
  dbConnections: sqlObjectsRoot.ConnectionsState;
  sqlObject: any;
  router: fromRouter.RouterReducerState<any>;
}

export const reducers: ActionReducerMap<DBManagerState> = {
  sqlObjects: sqlObjectsRoot.sqlObjectsReducer,
  dbConnections: sqlObjectsRoot.connectonsReducer,
  sqlObject: sqlObjectRoot.sqlObjectsReducer,
  router: fromRouter.routerReducer
};

export const metaReducers: MetaReducer<DBManagerState>[] = !environment.production ? [] : [];
