import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { of } from 'rxjs';
import { catchError, switchMap, map } from 'rxjs/operators';
import { ActionTypes, LoadedSqlObjectsAction } from '../actions/sql-objects';

import { NotificationService, SqlDbService } from 'src/app/shared/services';
import { environment } from 'src/environments/environment';
import * as rootActions from '../actions/index';
import { NotificationType } from 'src/app/shared/models';

export * from './sql-object';

@Injectable()
export class DBManagerEffects {
  constructor(
    private actions$: Actions,
    private notificationService: NotificationService,
    private sqlDb: SqlDbService) {}

  @Effect()
  loadSqlObjects$ = this.actions$.pipe(
    ofType(ActionTypes.LOAD_SQL_OBJECTS),
    switchMap(({payload}) => this.sqlDb.getSqlObjects(`${environment.apiBaseUrl}/sql/${payload}`)),
    map(objects => new LoadedSqlObjectsAction(objects)),
    catchError(e => of(new rootActions.ErrorAction(e.message))));

  @Effect()
  errorHandler$ = this.actions$.pipe(
    ofType(rootActions.ActionTypes.ERROR),
    map(({payload}) => {
      this.notificationService.open({
        message: payload,
        type: NotificationType.DANGER
      });
      return {type: `[DB Manager] Shown Error: ${payload}`};
    }));

  @Effect()
  successHandler$ = this.actions$.pipe(
    ofType(rootActions.ActionTypes.SUCCESS),
    map(({payload}) => {
      this.notificationService.open({
        message: payload,
        type: NotificationType.SUCCESS
      });
      return {type: `[DB Manager] Shown Success: ${payload}`};
    }));
}
