import { Component, OnInit, ElementRef, ViewChild, HostListener } from '@angular/core';
import { Ace, edit } from 'ace-builds';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';
import { of } from 'rxjs';
import { distinctUntilChanged, catchError } from 'rxjs/operators';
import { selectConnectionName } from '../../store/selectors';
import { DBManagerState } from '../../store';
import { environment } from 'src/environments/environment.prod';
import { mapTableToValues } from 'src/app/shared';
import { SqlTableData, SqlTypesToWijmoMap } from 'src/app/shared/models';
import { AuthService } from 'src/app/security';
import { ColumnInfo } from 'src/app/wijmo/models';

@Component({
  selector: 'app-sql-query',
  templateUrl: './sql-query.component.html',
  styleUrls: ['./sql-query.component.scss']
})
export class SqlQueryComponent implements OnInit {

  @ViewChild('sqlElement', {static: true}) sqlElement: ElementRef;
  connection: string;
  sqlEditor: Ace.Editor;
  fields: ColumnInfo[];
  data: any[];
  error: string;

  @HostListener('document:keydown', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    if (event.key === 'F5') {
      event.preventDefault();
      event.stopImmediatePropagation();
      this.onRun();
    }
  }

  constructor(
    private http: HttpClient,
    private store: Store<DBManagerState>,
    private authService: AuthService) { }

  ngOnInit() {
    this.store.select(selectConnectionName).pipe(
      distinctUntilChanged()).subscribe(connection => {
        this.connection = connection;
    });

    this.sqlEditor = edit(this.sqlElement.nativeElement, {
      mode: 'ace/mode/sql',
      autoScrollEditorIntoView: true
    });
  }

  onRun() {
    this.http.post<SqlTableData>(`${environment.apiBaseUrl}/sql/${this.connection}/execute`, {
      sql: this.sqlEditor.getSelectedText() || this.sqlEditor.getValue()
    }, this.authService.getHttpHeaders()).pipe(
      catchError(e => {
        this.error = typeof e.error === 'string' ? e.error : 'Failed to query data.';
        return of(null);
      })
    ).subscribe(res => {
      if (res) {
        this.error = null;
        this.fields = res.fields.map(f => {
          return {
            bindingName: f.fieldName,
            dataType: SqlTypesToWijmoMap[f.dataType],
            headerText: f.fieldName
          };
        });
        this.data = mapTableToValues(res.table);
      } else {
        this.fields = null;
        this.data = null;
      }
    });
  }

}
