import { createSelector, createFeatureSelector } from '@ngrx/store';

import { DBManagerState } from '../index';
import { SqlObjectState } from '../reducers/sql-object';
import { compareSqlObjects } from 'src/app/shared';
import { LoadingMessages } from '../../models';

export const selectSqlObject = createFeatureSelector<DBManagerState, SqlObjectState>('sqlObject');

export const selectSqlObjectStructure = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.structure);

export const selectSqlObjectData = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.data);

export const selectSqlObjectFieldsState = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.currentState.fields);

export const selectIsChangedSqlObjectStructure = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => !compareSqlObjects(state.structure, state.currentState));

export const selectSqlObjectState = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.currentState);

export const selectSqlObjectLoading = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.loading);

export const selectIsChangedSqlObjectData = createSelector(
  selectSqlObject,
  ({changedData}: SqlObjectState) => {
    return Boolean(changedData && changedData.length);
  });

export const selectSqlObjectLoadingMessages = createSelector(
  selectSqlObjectLoading,
    ((loading) => {
      if (loading.length) {
        return {
          messages: loading.map(item => LoadingMessages[item])
        };
      }
      return null;
    }));

export const selectSqlObjectRowsCount = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.rowsCount);

export const selectSqlObjectLimitRows = createSelector(
  selectSqlObject,
  (state: SqlObjectState) => state.filters.limitRowsCount);
