import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { DBManagerState } from '../../store';
import { selectIsChangedSqlObjectStructure, selectIsChangedSqlObjectData, selectSqlObjectStructure,
  selectSqlObjectLoadingMessages } from '../../store/selectors';
import { MetaDataObject } from 'src/app/shared/models';

@Component({
  selector: 'app-sql-object-content',
  templateUrl: './sql-object-content.component.html',
  styleUrls: ['./sql-object-content.component.scss']
})
export class SqlObjectContentComponent implements OnInit, OnDestroy {

  loading$: Observable<any>;
  destroy$ = new Subject();
  isStructureChanged$: Observable<boolean>;
  isDataChanged$: Observable<boolean>;
  dataObject: MetaDataObject;

  constructor(private store: Store<DBManagerState>) { }

  ngOnInit() {
    this.isStructureChanged$ = this.store.select(selectIsChangedSqlObjectStructure);
    this.isDataChanged$ = this.store.select(selectIsChangedSqlObjectData);

    this.store.select(selectSqlObjectStructure).pipe(
      takeUntil(this.destroy$)).subscribe(obj => {
        this.dataObject = obj as MetaDataObject;
      });

    this.loading$ = this.store.select(selectSqlObjectLoadingMessages);
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
