import { Component, OnInit, OnDestroy, ViewChild, Input } from '@angular/core';
import { Actions, ofType } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { Observable, Subject, combineLatest } from 'rxjs';
import { takeUntil, withLatestFrom, switchMap, filter } from 'rxjs/operators';
import { FlexGrid, CellRangeEventArgs, CellType } from 'wijmo/wijmo.grid';
import { TableColumn, SQLDataTypes, SqlTypesToWijmoMap, TableDto, MetaDataObject, FileType } from 'src/app/shared/models';
import { DBManagerState } from '../../store';
import {
  selectSqlObjectData, selectSqlObjectFieldsState, selectIsChangedSqlObjectData,
  selectSqlObjectLoadingMessages,
  selectSqlObjectLimitRows,
  selectSqlObjectStructure,
  selectSqlObjectRowsCount,
  selectConnectionName
} from '../../store/selectors';
import {
  SaveSqlObjectDataAction, ActionTypes, UpdateSqlObjectDataAction,
  SetSqlDataFilterPropAction,
  SetSqlDataFiltersAction,
  LoadSqlObjectDataAction
} from '../../store/actions/sql-object';
import { initChangesHighlight, GridContext } from 'src/app/wijmo/utils';
import { mapTableToValues, getFilters, deepClone, dateToDtoString, formatWijmoDate, compareFields } from 'src/app/shared';
import { ColumnEditorComponent } from 'src/app/wijmo/components/column-editor/column-editor.component';
import { DialogService, FileManagerService } from 'src/app/shared/services';
import { ColumnInfo } from 'src/app/wijmo/models';
import { GrigConfig } from 'src/app/wijmo/components/grid-object-explorer/grid-config';
import { DataType } from 'wijmo/wijmo';
import { FlexGridXlsxConverter } from 'wijmo/wijmo.grid.xlsx';

/**
 * @description Displays table data.
 */
@Component({
  selector: 'app-table-data',
  templateUrl: './table-data.component.html',
  styleUrls: ['./table-data.component.scss']
})
export class TableDataComponent implements OnInit, OnDestroy {

  data: any[];
  fields$: Observable<TableColumn[]>;
  isChanged$: Observable<boolean>;
  loading$: Observable<any>;
  // ToDo: What type it is? it is bound to array
  filters: { [key: string]: any[] } = {};
  fields: ColumnInfo[];
  currentPage = 1;
  limitRows: number;
  dataObject: MetaDataObject;
  connection: string;
  localGridConfig: GrigConfig;
  @ViewChild('grid', { static: true }) grid: FlexGrid;
  @Input() isReadOnly: boolean;

  private gridContext: GridContext;
  private destroy$ = new Subject();
  private columnToEdit: ColumnInfo;
  private contextMenuValue: any;
  private rowsCount: number;

  constructor(
    private store: Store<DBManagerState>,
    private actions$: Actions,
    private dialogService: DialogService,
    private fileService: FileManagerService) { }

  ngOnInit() {
    this.isChanged$ = this.store.select(selectIsChangedSqlObjectData);
    this.fields$ = this.store.select(selectSqlObjectFieldsState).pipe(
      takeUntil(this.destroy$));

    this.fields$.subscribe(fields => {
      this.fields = fields.map(f => {
        return {
          isKey: f.isKey,
          bindingName: f.fieldName,
          sqlDataType: f.dataType,
          headerText: this.getColumnHeader(f.fieldName),
          width: this.getColumnWidth(f.fieldName),
          dataType: this.getColumnType(f.fieldName, f.dataType),
          displayFormat: this.getColumnDisplayFormat(f.fieldName)
        };
      });
    });

    this.store.select(selectConnectionName).subscribe(connection => {
      this.connection = connection;
    });

    combineLatest([this.fields$, this.store.select(selectSqlObjectData)]).pipe(
      filter(([fields]) => fields && fields.length !== 0),
      takeUntil(this.destroy$)).subscribe(([fields, data]) => {
        this.refreshData(data);
      });

    this.actions$.pipe(
      ofType(ActionTypes.REFRESH_SQL_OBJECT),
      takeUntil(this.destroy$),
      withLatestFrom(this.store)).subscribe(([action, { sqlObject: { data } }]) => {
        this.refreshData(data);
        this.currentPage = 1;
      });

    this.loading$ = this.store.select(selectSqlObjectLoadingMessages);

    this.store.select(selectSqlObjectLimitRows).subscribe(limit => {
      this.limitRows = limit;
    });
    this.store.select(selectSqlObjectRowsCount).subscribe(rowsCount => {
      this.rowsCount = rowsCount;
    });
    this.store.select(selectSqlObjectStructure).subscribe((obj) => {
      this.dataObject = obj;
      this.filters = {};
      this.currentPage = 1;
      this.localGridConfig = new GrigConfig(`database-${this.connection}`, `${obj.objectOwner}-${obj.objectName}`);
      setTimeout(() => {
        this.grid.autoSizeRow(0, true);
      });
    });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  saveData() {
    this.store.dispatch(new SaveSqlObjectDataAction());
  }

  getColumnWidth(binding: string, defaultWidth: number | '*' = 150) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding).width) || defaultWidth;
  }

  getColumnHeader(binding: string) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding).header);
  }

  getColumnType(binding: string, type: SQLDataTypes) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding).dataType) || SqlTypesToWijmoMap[type];
  }

  getColumnDisplayFormat(binding: string) {
    return (this.localGridConfig && this.localGridConfig.getColumn(binding).displayFormat) || 'dd/MM/yyyy';
  }

  initializedGrid(grid: FlexGrid) {
    this.gridContext = initChangesHighlight(grid);
    grid.cellEditEnded.addHandler(() => this.changeHandler());
    grid.deletedRow.addHandler(() => this.changeHandler());

    grid.addEventListener(grid.hostElement, 'contextmenu', (e: CellRangeEventArgs) => {
      const ht = grid.hitTest(e);
      this.columnToEdit = this.fields.find((v, i) => ht.col === i);
      const val = this.grid.collectionView.items[ht.row][this.columnToEdit.bindingName];
      this.contextMenuValue = val;
    });

    const sItemsFormatter = grid.itemFormatter;

    grid.itemFormatter = (panel, r, c, cell) => {
      sItemsFormatter(panel, r, c, cell);
      const binding = panel.columns[c].binding;
      if (panel.cellType === CellType.ColumnHeader && cell.firstChild instanceof HTMLElement) {
        const sortSpan = cell.firstChild.querySelector('#sort-icon');
        if (sortSpan) {
          cell.firstChild.removeChild(sortSpan);
        }
        const sortState = grid.collectionView.sortDescriptions[0];
        if (sortState && sortState.property === binding) {
          const span = document.createElement('SPAN');
          span.id = 'sort-icon';
          if (sortState.ascending) {
            span.className = 'wj-glyph-up';
          } else {
            span.className = 'wj-glyph-down';
          }
          cell.firstChild.appendChild(span);
        }
      }
    };

    grid.sortingColumn.addHandler((s, e: any) => {
      if (e.data.target.classList.contains('fa-filter')) {
        e.cancel = true;
      }
    });

    grid.resizingColumn.addHandler((s, evt: any) => {
      if (this.localGridConfig) {
        this.localGridConfig.saveColumn(grid.columns[evt.col].binding, {
          width: grid.columns[evt.col].renderWidth
        });
      }
    });
  }

  exportToCSV() {
    this.fileService.exportToFile(this.grid.collectionView.sourceCollection, {
      fileType: FileType.CSV,
      fileName: `${this.dataObject.objectOwner}-${this.dataObject.objectName}`
    });
  }

  exportToExcel() {
    FlexGridXlsxConverter.saveAsync(this.grid, {
      includeColumnHeaders: true
    }, `${this.dataObject.objectOwner}-${this.dataObject.objectName}`);
  }

  onCtxMenuClicked(menu) {
    switch (menu.value) {
      case 'edit-column': this.editColumn(); break;
      case 'fill-all': this.fillRows('all'); break;
      case 'fill-empty': this.fillRows('empty'); break;
    }
  }

  private editColumn() {
    const columnEditDialog = this.dialogService.open(ColumnEditorComponent, { class: 'modal-lg' });
    const columnInfo = this.columnToEdit;

    columnEditDialog.config = [columnInfo];
    columnEditDialog.init.pipe(switchMap(() => columnEditDialog.component.save)).subscribe((columns) => {
      (columns as ColumnInfo[]).forEach(c => {
        this.localGridConfig.saveColumn(c.bindingName, c);
      });
      const column = columns[0];
      const index = this.fields.findIndex(f => f.bindingName === column.bindingName);
      this.fields.splice(index, 1, column);
    });
  }

  private fillRows(key: string) {
    let isUpdated = false;
    this.grid.collectionView.sourceCollection.forEach((item) => {
      const binding = this.columnToEdit.bindingName;
      const oldVal = item[binding];
      if (this.updateCondition(oldVal, key)) {
        item[binding] = this.contextMenuValue;
        this.gridContext.setEditValue(item, binding, oldVal, this.contextMenuValue);
        isUpdated = true;
      }
    });
    if (isUpdated) {
      this.grid.refresh(true);
      this.changeHandler();
    }
  }

  private updateCondition(oldVal: any, key: string): boolean {
    switch (key) {
      case 'all': return oldVal !== this.contextMenuValue;
      case 'empty': return [null, undefined, ''].includes(oldVal);
      default: return true;
    }
  }

  private changeHandler() {
    const { added, edited } = this.gridContext.state;
    let data = [];
    if (edited) {
      data = [...data, ...edited];
    }
    if (added) {
      data = [...data, ...added];
    }
    this.store.dispatch(new UpdateSqlObjectDataAction(data));
  }

  onFiltersChange(filters: { [key: string]: any[] }) {
    this.filters = { ...filters };
    this.applyFilters();
  }

  onApply(evt: any[], field: ColumnInfo) {
    this.filters = { ...this.filters, [field.bindingName]: evt };
    this.applyFilters();
  }

  get skipRows(): number {
    return (this.currentPage - 1) * this.limitRows;
  }

  get filtersList() {
    return Object.entries(this.filters).filter(([fieldName, value]) => Boolean(value)).map(([fieldName, value]) => {
      return {
        fieldName, value: value.map(v => {
          const type = this.localGridConfig.getColumn(fieldName).dataType;
          if (type === DataType.Date) {
            const displayFormat = this.localGridConfig.getColumn(fieldName).displayFormat;
            return formatWijmoDate(v, displayFormat);
          }
          if (v instanceof Date) {
            return dateToDtoString(v);
          }
          if (v === null) {
            return '[NULL]';
          }
          return v;
        }).join(', ')
      };
    });
  }

  onNextPage(val: number): void {
    if (this.currentPage >= 1 && val > 0 && this.skipRows + this.limitRows < this.rowsCount) {
      this.currentPage++;
    } else if (this.currentPage >= 2 && val < 0) {
      this.currentPage--;
    } else {
      return;
    }
    if (this.limitRows) {
      this.store.dispatch(new SetSqlDataFilterPropAction({ skipRowsCount: this.skipRows }));
    }
  }

  private applyFilters(): void {
    this.store.dispatch(new SetSqlDataFiltersAction({ filters: getFilters(this.filters) }));
    this.store.dispatch(new LoadSqlObjectDataAction(this.dataObject));
  }

  private refreshData(data: TableDto): void {
    this.data = this.formatData(deepClone(data));
    if (this.gridContext) {
      this.gridContext.clear();
    }
  }

  private formatData(data: TableDto): Object[] {
    if (data && data.fieldNames) {
      data.dataTypes = data.fieldNames.map(field => {
        const fieldData = this.fields.find(({ bindingName }) => bindingName === field);
        return fieldData ? fieldData.sqlDataType : null;
      });
      return mapTableToValues(data);
    }
    return [];
  }
}
