import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { takeUntil, distinctUntilChanged, switchMap, map, filter } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { LayoutConfig } from 'src/app/shared/models';
import { DBManagerState } from '../../store';
import { selectRouterParams, selectRouterParam, selectUrl } from '../../store/selectors';
import { LoadSqlObjectStructureAction, LoadSqlObjectDataAction,
  LoadedSqlObjectDataAction, LoadedSqlObjectStructureAction } from '../../store/actions/sql-object';
import { SelectConnection } from '../../store/actions/sql-objects';
import { getObjectTypeFromUrl } from '../../utils';

/**
 * @description Layout for database manager page.
 */
@Component({
  selector: 'app-manager-layout',
  templateUrl: './manager-layout.component.html',
  styleUrls: ['./manager-layout.component.scss']
})
export class ManagerLayoutComponent implements OnInit, OnDestroy {

  layoutConfig: LayoutConfig = {
    icon: 'assets/img/rc-logo-64x64.png',
    smIcon: 'assets/img/rc-logo-36x36.png',
    appName: 'database-manager'
  };
  private destroy$ = new Subject();

  constructor(private store: Store<DBManagerState>) { }

  ngOnInit() {
    this.store.select(selectRouterParam, 'connectionName').pipe(
      filter(connection => Boolean(connection)),
      distinctUntilChanged(),
      takeUntil(this.destroy$)).subscribe(connection => {
      this.store.dispatch(new SelectConnection(connection));
    });

    this.store.select(selectUrl).pipe(
      takeUntil(this.destroy$),
      switchMap(url => {
        return this.store.select(selectRouterParams, ['ownerName', 'objectName']).pipe(
          distinctUntilChanged((x, y) => JSON.stringify(x) === JSON.stringify(y)),
          takeUntil(this.destroy$),
          map(params => ({params, url})));
      })).subscribe(({params, url}) => {
        const {ownerName, objectName} = params;
        if (ownerName && objectName) {
          this.store.dispatch(new LoadSqlObjectStructureAction({
            objectOwner: ownerName,
            objectName,
            objectType: getObjectTypeFromUrl(url)
          }));

          this.store.dispatch(new LoadSqlObjectDataAction({
            objectOwner: ownerName,
            objectName
          }));
        } else {
          this.store.dispatch(new LoadedSqlObjectDataAction(null));
          this.store.dispatch(new LoadedSqlObjectStructureAction(null));
        }
      });
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
