import { Action } from '@ngrx/store';

/** @description Enum sql object list actions. */
export enum ActionTypes {
  LOAD_SQL_OBJECTS = '[DB Manager] load sql objects',
  LOADED_SQL_OBJECTS = '[DB Manager] loaded sql objects',

  SELECT_CONNECTION = '[DB Manager] select connection',
  LOADED_CONNECTIONS = '[DB Manager] loaded connections',
}

export class LoadSqlObjectsAction implements Action {
  readonly type = ActionTypes.LOAD_SQL_OBJECTS;

  constructor(public payload: string) {}
}

export class LoadedSqlObjectsAction implements Action {
  readonly type = ActionTypes.LOADED_SQL_OBJECTS;

  constructor(public payload: any[]) {}
}

export class SelectConnection implements Action {
  readonly type = ActionTypes.SELECT_CONNECTION;

  constructor(public payload: string) {}
}

export class LoadedConnections implements Action {
  readonly type = ActionTypes.LOADED_CONNECTIONS;

  constructor(public payload: any[]) {}
}


export type ActionsUnion = LoadSqlObjectsAction | LoadedSqlObjectsAction | SelectConnection | LoadedConnections;
