import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManagerLayoutComponent } from './components/manager-layout/manager-layout.component';
import { ConnectionGuard } from './guards/connection.guard';
import { SqlViewObjectComponent } from './components/sql-view-object/sql-view-object.component';
import { SqlObjectContentComponent } from './components/sql-object-content/sql-object-content.component';
import { SqlQueryComponent } from './components/sql-query/sql-query.component';


const routes: Routes = [{
  path: ':connectionName',
  component: ManagerLayoutComponent,
  canActivate: [ConnectionGuard],
  children: [{
    component: SqlObjectContentComponent,
    path: ''
  }, {
    component: SqlObjectContentComponent,
    path: ':ownerName/:objectName'
  }, {
    component: SqlViewObjectComponent,
    path: 'view'
  }, {
    component: SqlViewObjectComponent,
    path: 'view/:ownerName/:objectName'
  }, {
    component: SqlQueryComponent,
    path: 'sql-query'
  }]
}];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class DatabaseManagerRoutingModule { }
